import React from 'react';
import { ThemeToggler } from 'gatsby-plugin-dark-mode';
// import FontAwesome from 'react-fontawesome';
import { Switch } from 'antd';
import style from './toggler.module.less';

export default () => (
  <ThemeToggler>
    {({ theme, toggleTheme }) => (
      <>
        {/*
        <div className={style.themeTogglerDiv}>
          <FontAwesome name="sun-o" onClick={() => toggleTheme('light')} />
          <FontAwesome
            name={theme === 'dark' ? 'toggle-on' : 'toggle-off'}
            onClick={() => (theme === 'dark' ? toggleTheme('light') : toggleTheme('dark'))}
          />
          <FontAwesome name="moon-o" onClick={() => toggleTheme('dark')} />
        </div>
        */}

        <Switch
          className={style.themeToggler}
          checkedChildren={(<emoji>🌙</emoji>)}
          unCheckedChildren={<emoji>🔆</emoji>}
        //   unCheckedChildren={<emoji>☀️</emoji>}
          onChange={(checked) => (checked ? toggleTheme('dark') : toggleTheme('light'))}
          checked={theme === 'dark'}
        />

      </>
    )}
  </ThemeToggler>
);
